/*---------------------------------------------------bodyscrollbar css-------------------------------------------------------------------- */
body::-webkit-scrollbar {
    height: 0.7rem;
}

body::-webkit-scrollbar-track {
    background: transparent;

}

body::-webkit-scrollbar-thumb {
    background: transparent;
    border-radius: 10px;
}

body::-webkit-scrollbar-thumb:hover {
    background: #555;
}

/*---------------------------------------------------bodyscrollbar css-------------------------------------------------------------------- */

/*--------------------------------------------------------scrollbar css------------------------------------------------------------------- */
.scroll {
    padding: 0px;
    overflow-x: auto;
    overflow-y: auto;
    white-space: nowrap;
    height: max-content;
}

.scroll::-webkit-scrollbar {
    height: 0rem;
    width: 0rem;
}

.scroll::-webkit-scrollbar-track {
    background: transparent;
}

.scroll::-webkit-scrollbar-thumb {
    background: var(--charcoal75);
    border-radius: 65rem;
}

.scroll::-webkit-scrollbar-thumb:hover {
    background: #555;
}

