}
.pagination .active:focus{
    border-color:transparent ;
}
.pagination .btn :focus{
    color:#222023;
}

.dropdown-menu {
    animation-name: animatedropdown;
    animation-duration: 0.3s;
    /* transition:  1s; */
}
.navsection img{
    transition: all 0.5s;
}

.d-block ,button{
    animation-name: animateupdate;
    animation-duration: 0.3s;
}
/* .animatebuttons{
    animation-name: animateupdate;
    animation-duration: 0.3s;
} */
 @keyframes animatedropdown {
    from{
        transform: translateY(-3vh);

    }
    to{
        transform: translateY(4vh);
    }
 }


 @keyframes animateupdate {
    from{
        transform: translateX(-100%);

    }
    to{
        transform: translateX(0%);
    }
 }
@media (max-width:992px){
    body{
        background-size:contain;
    }
}
@media  (max-width:512px) {
    .pagination .btn{
        padding:0.275rem 0.65rem !important;
        font-size:0.7rem !important;
    }
    .pagination .page-link{
        padding:0.275rem 0.45rem !important;
        font-size:0.7rem !important;
    }
}

@media only screen and (-webkit-min-device-pixel-ratio: 2),
only screen and (min--moz-device-pixel-ratio: 2),
only screen and (min-device-pixel-ratio: 2) {
  /* CSS styles for devices with a minimum pixel ratio of 2 */
 
